'use client'; // <-- Must be first line for Client Component

import { Card, Button } from 'flowbite-react';
import Image from 'next/image';
import { useState } from 'react';

// Sample client data
const clients = [
  {
    name: 'Client A',
    logo: '/logos/clientA.png',
    testimonial: 'Excellent service! Highly recommended.',
  },
  {
    name: 'Client B',
    logo: '/logos/clientB.png',
    testimonial: 'Professional team and great support.',
  },
  {
    name: 'Client C',
    logo: '/logos/clientC.png',
    testimonial: 'Our project was delivered on time and exceeded expectations.',
  },
  {
    name: 'Client D',
    logo: '/logos/clientD.png',
    testimonial: 'Very responsive and innovative solutions.',
  },
  {
    name: 'Client E',
    logo: '/logos/clientE.png',
    testimonial: 'Top-notch expertise and communication.',
  },
];

export default function ClientsPage() {
  const [current, setCurrent] = useState(0);

  const nextTestimonial = () => setCurrent((prev) => (prev + 1) % clients.length);
  const prevTestimonial = () => setCurrent((prev) => (prev - 1 + clients.length) % clients.length);

  return (
    <div className="min-h-screen bg-gray-50 py-16 px-6 md:px-20">
      {/* Header */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-800">Our Clients</h1>
        <p className="text-gray-600 mt-2">Trusted by industry leaders worldwide</p>
      </div>

      {/* Clients Logo Grid */}
      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-8 items-center justify-items-center mb-16">
        {clients.map((client, idx) => (
          <div
            key={idx}
            className="transition transform hover:scale-105 duration-300 shadow-sm p-4 rounded-lg bg-white"
          >
            <Image src={client.logo} alt={client.name} width={150} height={80} className="object-contain" />
          </div>
        ))}
      </div>

      {/* Testimonial Carousel */}
      <div className="max-w-2xl mx-auto text-center mb-12">
        <Card className="p-6 shadow-lg">
          <p className="text-gray-700 italic">"{clients[current].testimonial}"</p>
          <p className="mt-4 font-semibold text-gray-900">{clients[current].name}</p>
          <div className="mt-4 flex justify-center gap-4">
            <Button color="gray" size="sm" onClick={prevTestimonial}>
              Prev
            </Button>
            <Button color="gray" size="sm" onClick={nextTestimonial}>
              Next
            </Button>
          </div>
        </Card>
      </div>

      {/* Call-to-Action */}
      <div className="text-center">
        <Button color="purple" size="lg">
          Work With Us
        </Button>
      </div>
    </div>
  );
}
